.seedprod-builder{
    display: none ;
}

.seedprod-action-txt{
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    width: 340px;
    display: inline-block;
}

#seedprod-page-name{
    width:100%;
    background:#fbfcfb;
    height: 75px;
    border-bottom:1px solid #f0f0f0;
    margin-top: 67px;
